

.App{ 
  .Main{
    padding: 0;
    margin: 0;
    .header{
      background-color: black;
      color: #fff;
      padding: 1em;
      text-align: center;
      h1{
        font-size: 1.5em;
        width: 100%;
      }
    }
    .fa-bars{
      position: fixed;
      top: 10px;
      right: 5px;
      width: 40px;
      font-size: 25px;
      background-color: #777;
      color: #fff;
      text-align: center;
    }
    nav{
      padding: 1em 0;
      ul{
        position: fixed;
        top: 30px;
        background-color: #777;
        width: 100%;
        text-align: center;
        display: none;
        li{
          a{
            color: #fff;
            &:hover{
              color: #777;
            }
          }
        }
      }
      .display{
        display: block;
      }
    }
    .search{
      padding: 0;
      .input-text{
        display: flex;
        justify-content: space-between;
        input[type=text]{
          width: 70%;
        }
        input[type=number]{
          width: 30%;
        }
      }
    }
    .loading{
      width: 50%;
    }
    .jumbotron{
      margin: 2em 0;
    }
    .imgPoster{
      border: 2px solid #000;
      width: 100%;
    }
    

    .moviecard{
      margin: 1em 0;
    }
    .card{
      padding: 3em 2em;
      margin: 1em 0 ;
      text-align: center;
    }
  }
}